import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'lib-flexible/flexible'
import  axios from 'axios'
import VueAxios from 'vue-axios'
import VueCookies from 'vue-cookies'


import {
  Button,
  NavBar,
  Field,
  Popup,
  Tabbar,
  TabbarItem,
  Toast,
  Search,
  Swipe,
  SwipeItem,
  Stepper,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  Checkbox,
  SwipeCell,
  SubmitBar,
  List,
  Empty,
  AddressList,
  AddressEdit,
  Cell,
  Dialog,
  Icon,
  Tab,
  Tabs,
  Uploader,


} from 'vant'

Vue
  .use(Button)
  .use(NavBar)
  .use(Field)
  .use(Popup)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Search)
  .use(Swipe)
  .use(SwipeItem)
  .use(Toast)
  .use(Stepper)
  .use(GoodsAction)
  .use(GoodsActionIcon)
  .use(GoodsActionButton)
  .use(Checkbox)
  .use(SwipeCell)
  .use(SubmitBar)
  .use(List)
  .use(Empty)
  .use(AddressList)
  .use(AddressEdit)
  .use(Cell)
  .use(Dialog)
  .use(Icon)
  .use(Tab)
  .use(Tabs)
  .use(Uploader)


Vue.use(VueAxios,axios)
Vue.use(VueCookies)
axios.interceptors.request.use(config => {
  if(config.method == 'post'){
    let paramsStr = "";
    for(let key in config.data) {
      paramsStr += `${key}=${config.data[key]}&`;
    }
    config.data = paramsStr.slice(0,-1);
  }
  return config
})

//将请求路径存放在原型属性中
Vue.prototype.baseUrl = 'http://www.kangliuyong.com:10002';
Vue.prototype.appkey = "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=";

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
