const MainRoutes = [
  {
    path:'/home',
    name:"Home",
    alias:'/',
    component:() => import('../views/Home.vue')
  },
  {
    path:'/menu',
    name:"Menu",
    component:() => import('../views/Menu.vue')
  },
  {
    path:'/shopcar',
    name:"Shopcar",
    component:() => import('../views/Shopcar.vue')
  },
  {
    path:'/my',
    name:"My",
    component:() => import('../views/My.vue')
  }
];

export const routes = [
    {
      path: '/',
      name: 'Main',
      component: () => import('../views/Main.vue'),
      children:MainRoutes
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('../views/Login.vue')
    },
    {
      path:'/detail/:pid',
      name:'Detail',
      component:() => import('../views/Detail.vue')
    },
    {
      path:'/addresslist',
      name:'AddressList',
      component:() => import('../views/AddressList.vue')
    },
    {
      path:'/address/:aid?',
      name:'Address',
      component:() => import('../views/Address.vue')
    },
    {
      path:"/searchresult/:goodsname",
      name:"SearchResult",
      props:true,
      component:() => import('../views/SearchResult.vue')
    },
    {
      path:"/userdata",
      name:"UserData",
      props:true,
      component:() => import('../views/UserData.vue')
    },
    {
      path:"/myorders",
      name:"MyOrders",
      props:true,
      component:() => import('../views/MyOrders.vue')
    },
    {
      path:"/mylike",
      name:"MyLike",
      props:true,
      component:() => import('../views/MyLike.vue')
    },
    {
      path:"/safecenter",
      name:"SaveCenter",
      props:true,
      component:() => import('../views/SafeCenter.vue')
    },
    {
      path:"/pay",
      name:"Pay",
      props:true,
      component:() => import('../views/Pay.vue')
    },

]