import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    //购物车商品数量
    shopbagCount:0,
    //是否已经加载购物车商品数量
    isLoadShopbagCount:false,
    //用户信息
    userData:{},
    //是否已经加载用户信息
    isLoadUserdata:false,
    //是否用新的订单信息
    isNewOrderMsg:false,
    //是否有新的收藏
    isNewLike:false,
    //是否加载用户信息
  },
  mutations: {
    //修改购物车数量
    changeShopbagCount(state,data){
      state.shopbagCount = data;
    },
    //修改购物车数量加载状态
    changeIsLoadShopbagCount(state,data){
      state.isLoadShopbagCount = data;
    },
    //修改用户信息
    changeUserData(state,data){
      state.userData = data
    },
    //修改用户信息加载状态
    changeIsLoadUserdata(state,data){
      state.isLoadUserdata = data
    },
    //修改是否新的订单信息
    changeNewOrderMsg(state,data){
      state.isNewOrderMsg = data;
    },
    //修改是否有新的收藏
    changeNewLike(state,data){
      state.isNewLike = data;
    },
    //修改用户头像
    changeUserHeader(state,img){
      state.userData.userImg = img;
    },
    // 修改用户背景图
    changeUserBg(state,img){
      state.userData.userBg = img;
    }
  },
  actions: {
  },
  modules: {
  }
})
